.modal {
  width: 300px;
  margin: auto;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  position: relative;
}

.modal h2 {
  margin-top: 0;
}

.modal form {
  display: flex;
  flex-direction: column;
}

.modal form input {
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.modal form button {
  padding: 10px;
  border-radius: 4px;
  border: none;
  background: var(--background-color-1);
  color: #fff;
  cursor: pointer;
}

.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}
